.orb-header { color: #333; border-bottom: 1px solid #CCC; }

.orb-header__blocks { display: block; padding-top: 4px; padding-bottom: 4px; width: 74px; }

@media (min-width: 37.5em) { .orb-header__blocks { padding-top: 8px; padding-bottom: 8px; width: 84px; } }

.orb-header__blocks img { display: block; height: 21px; width: inherit; }

@media (min-width: 37.5em) { .orb-header__blocks img { height: 24px; width: inherit; } }

/* ---- reset ---- */
body { margin: 0; font: normal 75% Arial, Helvetica, sans-serif; }

canvas { display: block; vertical-align: bottom; }

/* ---- particles.js container ---- */
#particles-js { position: absolute; width: 100%; height: 100%; background-color: #b61924; background-image: url("http://upload.wikimedia.org/wikipedia/commons/thumb/e/e5/NASA_logo.svg/1237px-NASA_logo.svg.png"); background-repeat: no-repeat; background-size: 20%; background-position: 50% 50%; }

/* ---- stats.js ---- */
.count-particles { background: #000022; position: absolute; top: 48px; left: 0; width: 80px; color: #13E8E9; font-size: .8em; text-align: left; text-indent: 4px; line-height: 14px; padding-bottom: 2px; font-family: Helvetica, Arial, sans-serif; font-weight: bold; }

.js-count-particles { font-size: 1.1em; }

#stats, .count-particles { -webkit-user-select: none; margin-top: 5px; margin-left: 5px; }

#stats { border-radius: 3px 3px 0 0; overflow: hidden; }

.count-particles { border-radius: 0 0 3px 3px; }

